* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  text-decoration: none;
  list-style: none;
  transition: all .3s;
}

.page {
  color: #929292;
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.page:has(.page__menu:target), .page:has(.page__language:target), .page:has(.page__faq:target), .page:has(.page__help:target) {
  overflow: hidden;
}

.page__container {
  max-width: 320px;
  margin-inline: auto;
  padding-inline: 20px;
}

@media (width >= 768px) {
  .page__container {
    max-width: 768px;
    margin-inline: auto;
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .page__container {
    max-width: 1280px;
    margin-inline: auto;
    padding-inline: 110px;
  }
}

@media (width >= 1920px) {
  .page__container {
    max-width: 1920px;
    margin-inline: auto;
    padding-inline: 234px;
  }
}

.page__section {
  padding-block: 50px;
}

@media (width >= 768px) {
  .page__section {
    padding-block: 70px;
  }
}

@media (width >= 1280px) {
  .page__section {
    padding-block: 100px;
  }
}

@media (width >= 1920px) {
  .page__section {
    padding-block: 160px;
  }
}

.page__body {
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  min-width: 320px;
}

.page__menu, .page__language, .page__faq, .page__help, .page__buy {
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  transition: all .5;
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target, .page__language:target, .page__faq:target, .page__help:target, .page__buy:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__title {
  color: #fff;
  font-size: 21px;
  font-weight: 900;
  line-height: 1.2;
}

@media (width >= 1280px) {
  .page__title {
    font-size: 36px;
  }
}

.page__title--accent {
  color: #05c2df;
  font-weight: 400;
}

.red {
  border: 1px solid green;
}

.active {
  display: block;
}

