.dropdown {
  position: relative;
}

.dropdown__trigger {
  cursor: pointer;
  background-color: inherit;
  color: #05c2df;
  border: none;
  gap: 4px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.3;
  transition: transform .3s;
  display: flex;
}

.dropdown__trigger:after {
  content: url("arrow-down.09ac9dd7.svg");
}

.dropdown__trigger:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.dropdown__content {
  transform-origin: top;
  opacity: 0;
  pointer-events: none;
  background-color: #191536;
  border-radius: 4px;
  margin-top: 6px;
  position: absolute;
  transform: translateY(-5px);
}

.dropdown__list {
  color: #fff;
  flex-direction: column;
  gap: 12px;
  padding: 10px;
  font-weight: 500;
  line-height: 1.3;
  display: flex;
}

.dropdown--active .dropdown__content {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.dropdown__item {
  cursor: pointer;
  transition: color .3s;
}

.dropdown__item:hover {
  cursor: pointer;
  color: #05c2df;
}

.nav__list {
